import { createAdder } from "../../common";
import addSorting from "./lib/sorting";
import addSampling from "./lib/sampling";
import addRanking from "./lib/ranking";
import addUncleaning from "./lib/uncleaning";

function init() {
	var proto = {};
	var add = createAdder(proto);
	addSorting(add);
	addSampling(add);
	addRanking(add);
	addUncleaning(add);
	return proto;
}

export default init;
