import { createAdder } from "../../common";
import addSorting from "./lib/sorting";
import addSummaries from "./lib/summaries";
import addSampling from "./lib/sampling";
import addRanking from "./lib/ranking";
import addUncleaning from "./lib/uncleaning";
import addGeographic from "./lib/geographic";

function init() {
	var proto = {};
	var add = createAdder(proto);
	addSorting(add);
	addSummaries(add);
	addSampling(add);
	addRanking(add);
	addUncleaning(add);
	addGeographic(add);
	return proto;
}

export default init;
